import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { CoursePurchaseService } from '../../course-purchase.service';
import { DetailsCourseComponent } from '../dialogs/details-course/details-course.component';
import { environment } from '../../../../../environments/environment';
@Component({
  selector: 'app-purchased',
  templateUrl: './purchased.component.html',
  styleUrls: ['./purchased.component.scss']
})
export class PurchasedComponent implements OnInit {
  displayedColumns: string[] = ['video', 'name', 'category','date', 'length','price','view'];
  listCourses:COURSE[];
  constructor(public dialog: MatDialog,public service:CoursePurchaseService) {}

  ngOnInit(): void {
    this.service.history().subscribe(res=>{
      let data = res.library;
      this.listCourses = [];
      for(const course of data){
        this.listCourses.push({
          id:course?._id,
          name:course?.course?.name,
          category:course?.course?.category,
          description:course?.course?.description,
          price:course?.course?.price,
          length:new Date(course?.course?.length * 1000).toISOString().substr(14, 5),
          poster:`${window.origin}/${course?.course?.thumbnail}`,
          // uploader:course?.owner?.firstName+' '+course?.owner?.lastName,
          video:course?.course?.file?`${window.origin}/${course?.course?.file}`:'',
          date:course?.buyDateTime
        })
      }
    })
  }
  openDialog(data) {
    const dialogRef = this.dialog.open(DetailsCourseComponent, {
      width: '700px',
      data: data,
    });

    dialogRef.afterClosed().subscribe((result) => {
      console.log(`Dialog result: ${result}`);
    });
  }

}

export interface COURSE {
  id:any;
  video?: string;
  poster?:string;
  name: string;
  category: string;
  uploader?: string;
  description?: string;
  length: string;
  price: number;
  date?: string;
}
